
  import { defineComponent } from 'vue'

  export default defineComponent({
    name: 'table',
    props:{
      listData:{
        type:Array,
        required:true
      },
      listCount:{
        type:Number,
        default:0
      },
      propsList:{
        type:Array,
        required:true
      },
      shouIndexColumn:{
        type:Boolean,
        default:false
      },
      showSelectColumn:{
        type:Boolean,
        default:false
      },
      title:{
        type:String,
        default:"默认列表"
      },
      page:{
        type:Object,
        default:()=>({currentPage:0,pageSize:10})
      },
      childrenProps:{
        type:Object,
        default:()=>({})
      },
      showFooter:{
        type:Boolean,
        default:true
      }
    },
    components: {},
    emits:['update:page'],
    setup(props,{emit}){
      const handleSizeChange=(pageSize:number)=>{
        emit('update:page',{...props.page,pageSize})
      }
      const handleCurrentChange=(currentPage:number)=>{
        emit('update:page',{...props.page,currentPage})
      }
      return{
        handleSizeChange,
        handleCurrentChange
      }
    }
  })
